html,
body {
  margin: 0;
  padding: 0;
}

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

.image-container {
  display: flex;
  flex-direction: column nowrap;
  height: auto;
  width: auto;
  height: 100%;
  width: 100%;
  position: relative;
}

.correct-guess-marker {
  display: flex;
  justify-content: center;
  align-items: center;
  color: green;
  background-color: rgba(255, 255, 255, 0.2);
  border-style: solid;
  border-color: white;
  font-weight: bold;
  font-size: 50px;
  width: 100px;
  height: 100px;
  z-index: 10;
}

.header-links {
  display: flex;
  gap: 22px;
  margin-top: 20px;
  justify-content: center;
}

.header-link {
  display: flex;
  justify-content: center;
  text-decoration: none;
  color: rgb(98, 98, 167);
  border: 1px solid gray;
  border-radius: 6px;
  padding: 15px;
  width: 100px;

  font-family: 'russo one';
  font-size: 20px;
}

a.active {
  background-color: rgba(201, 201, 201, 0.199);
}

.header-link:hover {
  background-color: rgb(224, 224, 224);
}

.header-buttons {
  display: flex;
  margin-top: 20px;
}

.timer-character-list-container {
  z-index: 220;
  position: fixed;
  right: 0;
  top: 0;
  background-color: rgba(201, 201, 201, 0.199);

  padding: 10px;

  cursor: pointer;
}
